storiesOf('Hooks|useTimeout', module).addParameters({ storySource: { source: __STORY__, locationsMap: __ADDS_MAP__ } }).addDecorator(withSourceLoader(__STORY__, __ADDS_MAP__,__MAIN_FILE_LOCATION__,__MODULE_DEPENDENCIES__,__LOCAL_DEPENDENCIES__,__SOURCE_PREFIX__,__IDS_TO_FRAMEWORKS__))
  .addParameters({
    readme: {
      // Show readme at the addons panel
      sidebar: README,
      StoryPreview: ({ children }) => <div>{children}</div>,
    },
  })
  .addDecorator(withKnobs)
  .add('default', () => {
    const beforeTimeoutText = text('Before Timeout', 'Hello');
    const afterTimeoutText = text('After Timeout', 'World');

    const Component = () => {
      const [value, setValue] = useState(beforeTimeoutText);
      const ready = useTimeout(number('Timeout', 3000));

      useEffect(() => {
        if (ready) {
          setValue(afterTimeoutText);
        }
      }, [ready]);

      return <span>{value}</span>;
    };

    return <Component />;
  });

